.container-input {
  background: var(--mainWhite) !important;
  align-items: center;
  color: var(--darkPink) !important;
  width: 40vh;
  margin: 20vh;
}

.container-table {
  background: transparent !important;
  align-items: center;
  color: var(--darkPink) !important;
  padding: 50px;
  margin: 20px;
  align-items: center;
  width: 40vh;
}

.table-header {
  margin: 1.5rem;
}

:root{
  --mainBlue: #5C999E;
  --mainPink: #E58C8A;
  --darkPink: #995852;
  --mainYellow: #E0C05D;
  --mainDark: #222222;
  --mainWhite: #F8F8F8;
  --mainGray: #E5E5E5;
}

body{
  font-family: 'Montserrat', sans-serif !important;
  background-color: var(--darkPink) !important;
  color: var(--mainDark) !important;
  
}

.text-yellow {
  color: var(--mainYellow) !important;
  text-transform: uppercase;
  text-align: center;
}

.text-light {
  color: var(--mainWhite) !important;
  text-transform: uppercase;
  text-align: center;
}

.wrapper {
  display: flex;  
  flex-flow: row wrap;
  font-weight: bold;
  text-align: center;
}

.wrapper > * {
  padding: 10px;
  flex: 1 100%;
  height: 100vh;
}

.aside-1 {
  background: var(--mainPink);
  color: var(--mainDark);
}

.aside-2 {
  background: var(--mainDark);
  color: var(--mainWhite);
}

@media all and (min-width: 600px) {
  .aside { flex: 1 0 0; }
}

@media all and (min-width: 800px) {
  .aside-1 { order: 1; } 
  .aside-2 { order: 2; }
}

.input-text {
  text-transform: capitalize;
}
